<template>
	<v-app>
    <v-content>
    	
    	<v-container>
    		<v-btn icon
					@click="goRoute('/')"
    		>
	        <v-icon>mdi-chevron-left</v-icon>
	      </v-btn>
        
	    </v-container>
      <v-text-field 
        color="primary" 
        loading=""
      ></v-text-field>
      <v-container>
	    	<form>
          <v-card outlined>
            <v-container fluid>
              <v-text-field
                dense
                outlined
                v-model="name"
                :error-messages="nameErrors"
                :counter="10"
                label=""
                required
                @input="$v.name.$touch()"
                @blur="$v.name.$touch()"
              ></v-text-field>
              <v-text-field
                dense
                outlined
                v-model="lastname"
                :error-messages="lastnameErrors"
                :counter="10"
                label=""
                required
                @input="$v.lastname.$touch()"
                @blur="$v.lastname.$touch()"
              ></v-text-field>
              <v-text-field
                dense
                outlined
                v-model="email"
                :error-messages="emailErrors"
                label="E-mail"
                required
                @input="$v.email.$touch()"
                @blur="$v.email.$touch()"
              ></v-text-field>
              <v-text-field
                dense
                outlined
                v-model="phone"
                :error-messages="phoneErrors"
                :counter="10"
                label=""
                required
                @input="$v.phone.$touch()"
                @blur="$v.phone.$touch()"
              ></v-text-field>
              <v-text-field
                outlined
                dense
                type="password"
                v-model="pass"
                :error-messages="passErrors"
                :counter="2"
                label=""
                required
                @input="$v.pass.$touch()"
                @blur="$v.pass.$touch()"
              ></v-text-field>
              <v-autocomplete
              dense
              outlined
                v-model="select"
                :items="items"
                :error-messages="selectErrors"
                required
                label=""
                @change="$v.select.$touch()"
                @blur="$v.select.$touch()"
              ></v-autocomplete>
            </v-container>
          </v-card>
          <v-container
            class="text-center"
          >
            <v-btn 
              depressed
              class="mr-4" 
              @click="submit"
              :color="'primary'"
            >
              
            </v-btn>
            <v-btn 
              depressed
              class="mr-4" 
              @click="goRoute('/login')"
            >
              
            </v-btn>
          </v-container>

			   <!--  <v-btn class="mr-4" @click="submit">submit</v-btn>
			    <v-btn @click="clear">clear</v-btn> -->
			  </form>
			</v-container>
    </v-content>
    <!-- <BottomNav/> -->
  </v-app>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, email } from 'vuelidate/lib/validators'
  // import BottomNav from '@/components/BottomNav.vue';

export default {
  name: 'Signup',
  components: {
    // BottomNav
  },
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    email: { required, email },
    select: { required },
    checkbox: {
      checked (val) {
        return val
      },
    },
  },

  data: () => ({
    name: '',
    email: '',
    select: null,
    items: [
      'Item 1',
      'Item 2',
      'Item 3',
      'Item 4',
    ],
    checkbox: false,
  }),

  computed: {
    checkboxErrors () {
      const errors = []
      if (!this.$v.checkbox.$dirty) return errors
      !this.$v.checkbox.checked && errors.push('You must agree to continue!')
      return errors
    },
    selectErrors () {
      const errors = []
      if (!this.$v.select.$dirty) return errors
      !this.$v.select.required && errors.push('Item is required')
      return errors
    },
    nameErrors () {
      const errors = []
      if (!this.$v.name.$dirty) return errors
      !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long')
      !this.$v.name.required && errors.push('Name is required.')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    },
  },

  methods: {
    submit () {
      this.$v.$touch()
    },
    clear () {
      this.$v.$reset()
      this.name = ''
      this.email = ''
      this.select = null
      this.checkbox = false
    },
    goRoute(url) {
      this.$router.push(url);
    }
  },
}
</script>
