<template>
	<v-app>
    <!-- <v-toolbar dense flat>
      <v-btn icon
        @click="goRoute('/')"
      >
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      
      <v-spacer></v-spacer>
    </v-toolbar> -->
    <v-container>
      <v-btn icon
        @click="goRoute('/')"
      >
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      
    </v-container>
    <v-text-field 
      color="primary" 
      loading=""
    ></v-text-field>
    <v-content>
    	<!-- <v-container>
        <v-btn icon
          @click="goRoute('/')"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        
      </v-container>
      <v-text-field 
        color="primary" 
        loading=""
      ></v-text-field> -->
      <v-container>
	    	<form>
          <v-card outlined>
            <v-container fluid>
    			    <v-text-field
                outlined
                dense
    			      v-model="email"
    			      :error-messages="emailErrors"
    			      label="E-mail"
    			      required
    			      @input="$v.email.$touch()"
    			      @blur="$v.email.$touch()"
    			    ></v-text-field>
              <!-- <v-text-field
                outlined
                dense
                v-model="name"
                :error-messages="nameErrors"
                :counter="10"
                label="Username"
                required
                @input="$v.name.$touch()"
                @blur="$v.name.$touch()"
              ></v-text-field> -->
              <v-text-field
                outlined
                dense
                type="password"
                v-model="pass"
                :error-messages="passErrors"
                :counter="2"
                label=""
                required
                @input="$v.pass.$touch()"
                @blur="$v.pass.$touch()"
              ></v-text-field>
            </v-container>
          </v-card>
          <v-container
            class="text-center"
          >
            <v-btn 
              depressed
              class="mr-4" 
              @click="submit"
              :color="'primary'"
            >
              
            </v-btn>

            <v-btn 
              depressed
              class="mr-4" 
              @click="goRoute('/signup')"
            >
              
            </v-btn>
          </v-container>
			    <!-- <v-btn class="mr-4" @click="submit">submit</v-btn>
			    <v-btn @click="clear">clear</v-btn> -->
			  </form>
			</v-container>
    </v-content>
  </v-app>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, minLength, email } from 'vuelidate/lib/validators'
  // import BottomNav from '@/components/BottomNav.vue';

export default {
  name: 'Login2',
  components: {
    // BottomNav
  },
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    email: { required, email },
    pass: { required, minLength: minLength(2) },
  },

  data: () => ({
    name: '',
    email: '',
    pass: ''
  }),

  computed: {
    nameErrors () {
      const errors = []
      if (!this.$v.name.$dirty) return errors
      !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long')
      !this.$v.name.required && errors.push('Name is required.')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    },
    passErrors () {
      const errors = []
      if (!this.$v.pass.$dirty) return errors
      !this.$v.pass.minLength && errors.push('Password must be at least 2 characters long')
      !this.$v.pass.required && errors.push('Password is required.')
      return errors
    },
  },

  methods: {
    submit () {
      this.$v.$touch()
    },
    clear () {
      this.$v.$reset()
      this.name = ''
      this.email = ''
      this.pass = ''
    },
    goRoute(url) {
      this.$router.push(url);
    }
  },
}
</script>
